@import url(https://fonts.googleapis.com/css?family=Raleway:200,400,700,800);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body{
  font-family: Raleway;
}

.App{
  position: absolute;
  right: 0px;
  left: 0px;
}

.sections{
  position: absolute;
  top: 0px;
  right: 0px;
  left: 0px;
  z-index: 0;
}
nav{
    width: calc(100% - 30px);
    padding: 15px;
    overflow: hidden;
    position: fixed;
    z-index: 100;
}

nav img{
    float: left;
    margin-top: 3px;
    margin-left: 10px;
}

nav ul {
    list-style: none;
    padding: 0px;
    margin: 0px;
    float: right;
    margin-right: 5px;
}

nav ul li {
    display: inline-block;
    margin: 5px 15px;
    font-size: 14px;
    color: white;
    font-weight: 700;
}

.scrolled{
    background-color: #2d2d2d !important;
    transition: background-color 100ms linear;
}
.hero{
    background: green;
    height: 100vh;
    background-image: linear-gradient(#1181d9, #182B42);
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    position: relative;
}

.hero h1{
    color: white;
    font-size: calc(20px + 2vw);
    font-weight: 400;
}

.hero h1 span{
    font-weight: 700;
}

.hero-container{
    opacity: 0.03;
    width: 100%;
    height: 100%;
    position: absolute;
}
.projects{
}

.projects .container{
    margin: auto;
    width: 850px;
    max-width: 80%;
    padding: 110px 0px;
}

.projects h1{
    font-weight: 700;
    font-size: 36px;
    color: #182e43;
    display: inline-block;
    margin: 0px;
}

.projects-grid{
    margin-top: 50px;
    width: calc(100% + 20px);
    text-align: center;
    margin-left: -10px;
}

@media only screen and (max-width: 850px) {
    .projects h1{
        font-size: 28px;
        margin-top: 5px;
    }
 }
 
.thumbnail{
    width: calc(33.333% - 20px);
    height: 170px;
    margin-bottom: 30px;
    border-radius: 10px;
    background: green;
    display: inline-block;
    margin: 10px;
    text-align: left;
    position: relative;
}

.thumbnail b{
    color: white;
    position: absolute;
    bottom: 15px;
    left: 15px;
}

@media only screen and (max-width: 850px) {
   .thumbnail{
        width: calc(50% - 20px);
   }
}

@media only screen and (max-width: 600px) {
    .thumbnail{
         width: calc(100% - 20px);
         margin: 10px;
    }
 }
.about {
  width: 100%;
  background-image: linear-gradient(#1181d9, #182b42);
}

.about-container {
  margin: auto;
  width: 850px;
  max-width: 80%;
  padding: 110px 0px;
}

.about-left {
  display: inline-block;
  width: calc(50% - 20px);
  padding-right: 60px;
  border-right: 1px solid #6e9cc5;
}

.about-right {
  float: right;
  width: calc(50% - 100px);
}

.about h1 {
  font-weight: 700;
  font-size: 36px;
  color: white;
  display: inline-block;
  margin: 0px;
  margin-bottom: 10px;
}

.about p {
  color: white;
}

.about a:hover {
  opacity: 0.5;
}

@media only screen and (max-width: 960px) {
  .about-right {
    width: calc(50% - 60px);
  }

  .about-left {
    width: calc(50% - 40px);
    padding-right: 30px;
  }
}

@media only screen and (max-width: 850px) {
  .about-right {
    margin: auto;
    padding: 0px;
    width: 100%;
    max-width: 400px;
    display: block;
    border: none;
    float: none;
    padding-top: 70px;
    border-top: 1px solid #6e9cc5;
  }

  .about-left {
    margin: auto;
    padding: 0px;
    width: 100%;
    max-width: 400px;
    display: block;
    border: none;
    margin-bottom: 70px;
  }
}

.orb{
    margin-bottom: 20px;
}

.orb-icon{
    height: 46px;
    width: 46px;
    border-radius: 50px;
    display: inline-block;
    border: 1px solid white;
}

.orb-icon img{
    margin-top: 11px;
    margin-left: 11px;
}

.orb .orb-text{
    height: 45px;
    line-height: 45px;
    vertical-align: bottom;
    margin-left: 20px;
    display: inline-block;
    font-weight: 700;
    color: white;
    font-size: 14px;
}
